import * as React from 'react';
import { Utils } from '@common/Utils';
import { User } from '@common/User';
import { Unit } from '@common/Unit';

import Highcharts from 'react-highcharts'; 
import HighchartsMore from 'highcharts-more';

import { objectLangs, lng } from '../../../../lngs/index';

HighchartsMore(Highcharts.Highcharts)

const ROOT_CLASS = 'product-page';

export class PopUpProductPage extends React.Component {
  render() {
    const { props } = this;

    const {
      rating,
      trader,
      balances,
      name,
      baseAsset,
      description,
    } = props;

    const {
      allTimeHigh,
      baseBalanceHistory,
      incomeAverage,
      maxDrawdown,
      maxRecoveryTimeShare,
      value,
    } = rating;

    const polarItems = {
      allTimeHigh,
      incomeAverage,
      maxDrawdown,
      maxRecoveryTimeShare,
      value,
    };

    return (
      <div className={ROOT_CLASS}>
        <div className={`${ROOT_CLASS}__user-block`}>
          <User
            nameProduct={name}
            theme="small"
            rating={rating}
            block="productPage"
          />
        </div>
        <div className={`${ROOT_CLASS}__infoblock`}>
          <div className={`${ROOT_CLASS}__infoblocktitle`}>Trader:</div>
          <div className={`${ROOT_CLASS}__infoblockcontent`}>{trader.firstName} {trader.lastName}</div>
        </div>  
        {
          rating && Object.keys(rating).length > 0 && !isNaN(rating.value) ?
            <React.Fragment>
              <div className={`${ROOT_CLASS}__infoblock`}>
                <div className={`${ROOT_CLASS}__infoblocktitle`}>Rating metrics:</div>
              </div>
              <div className={`${ROOT_CLASS}__ratingvars`}>
                <div className={`${ROOT_CLASS}__ratingvars__left`}>  
                  {<Highcharts 
                    config={{
                      chart: {
                        polar: true,
                        type: 'line',
                        backgroundColor: 'rgba(0,0,0,0)',

                        height: 200
                      },
                      title: {
                        text: null
                      },
                      pane: {
                        size: '80%'
                      },
                      xAxis: {
                        categories: Object.keys(polarItems),
                        tickmarkPlacement: 'on',
                        lineWidth: 0
                      },
                      yAxis: {
                        gridLineInterpolation: 'polygon',
                        lineWidth: 0,
                        min: 0
                      },
                      tooltip: {
                        shared: true,
                        pointFormat: '<span style="color:{series.color}"><b>{point.y:,.0f}</b><br/>'
                      },
                      legend: {
                        enabled: false
                      },   
                      credits: {
                        enabled: false
                      },           
                      series: [{
                        data: Object.keys(polarItems).map(cur => polarItems[cur]),
                        pointPlacement: 'on'
                      }]  
                    }}
                  />}
                </div>
                <div className={`${ROOT_CLASS}__ratingvars__right`}>
                  {
                    Object.entries(polarItems).map((cur, key) =>  
                      <div className={`${ROOT_CLASS}__ratingvars__right_item`} key={key}>
                        <span>{cur[0]}</span> : <span>{cur[1]}</span>
                      </div>
                    ) 
                  }
                </div>
              </div>
            </React.Fragment>
          :
            <div className={`${ROOT_CLASS}__infoblock`}>
              <div className={`${ROOT_CLASS}__infoblocktitle`}>Rating:</div>
              <div className={`${ROOT_CLASS}__infoblockcontent`}>N/A</div>
            </div>  
        }
        <div className={`${ROOT_CLASS}__infoblock`}>
          <div className={`${ROOT_CLASS}__infoblocktitle`}>Base asset:</div>
          <div className={`${ROOT_CLASS}__infoblockcontent`}>{baseAsset}</div>
        </div>
        {
          (typeof baseBalanceHistory === "object") ?
            <div className={`${ROOT_CLASS}__incomechart`}>
              <div className={`${ROOT_CLASS}__infoblock`}>
                <div className={`${ROOT_CLASS}__infoblocktitle`}>Income:</div>
              </div>  
              <Highcharts
                config={{
                  chart: {
                    // width: (baseBalanceHistory.length) * 35,
                    type: 'area',
                    backgroundColor: 'rgba(0,0,0,0)',
                    zoomType: 'x',
                  },
                  title: {
                    text: null
                  },
                  legend: {
                    layout: 'vertical',
                    align: 'left',
                    verticalAlign: 'top',
                    x: 150,
                    y: 100,
                    floating: true,
                    borderWidth: 1,
                    backgroundColor: '#FFFFFF'
                  },
                  xAxis: {
                    categories: baseBalanceHistory.map(cur => {
                      let _time = new Date(cur.timestamp);
                      return `${_time.getUTCDate()}.${_time.getUTCMonth()} ${_time.getUTCHours()}:${_time.getUTCMinutes()}`
                    }),
                    plotBands: [{ // visualize the weekend
                      from: 4.5,
                      to: 6.5,
                      color: 'rgba(68, 170, 213, .2)'
                    }]
                  },
                  yAxis: {
                    title: {
                      text: null
                    },
                    startOnTick: false,
                    endOnTick: false,
                    min: Math.min.apply(Math, baseBalanceHistory.map(cur => cur.value))
                  },
                  tooltip: {
                    shared: true,
                    valueSuffix: ` ${baseAsset}`
                  },
                  credits: {
                    enabled: false
                  },
                  plotOptions: {
                    areaspline: {
                      fillOpacity: 0.5
                    },
                    series: {
                      pointStart: 100
                    },
                  },
                  series: [{
                    name: baseAsset,
                    data: baseBalanceHistory.map(cur => cur.value),
                    step: true,
                    tooltip: {
                      valueDecimals: 6
                    },
                  }]  
                }}
              />
            </div>
          :
            <div className={`${ROOT_CLASS}__infoblock`}>
              <div className={`${ROOT_CLASS}__infoblocktitle`}>Income:</div>
              <div className={`${ROOT_CLASS}__infoblockcontent`}>No data yet</div>
            </div>  
        }
        <div className={`${ROOT_CLASS}__infoblock`}>
          <div className={`${ROOT_CLASS}__infoblocktitle`}>Name product:</div>
          <div className={`${ROOT_CLASS}__infoblockcontent`}>{name}</div>
        </div>
        <div className={`${ROOT_CLASS}__infoblock`}>
          <div className={`${ROOT_CLASS}__infoblocktitle`}>Description product:</div>
          <div className={`${ROOT_CLASS}__infoblockcontent`}>{description}</div>
        </div>
        <div className={`${ROOT_CLASS}__infoblock`}>
          <div className={`${ROOT_CLASS}__infoblocktitle`}>Exchange:</div>
          <div className={`${ROOT_CLASS}__infoblockcontent`}>Binance</div>
        </div>
        {
          (balances.length > 0) ?
            <div className={`${ROOT_CLASS}__ratingvars`}>
              <div className={`${ROOT_CLASS}__ratingvars__left`}>
                <Highcharts 
                  config={{
                    chart: {
                      height: 200,
                      plotBackgroundColor: null,
                      plotBorderWidth: null,
                      plotShadow: false,
       
                      backgroundColor: 'rgba(0,0,0,0)',
                      type: 'pie',
                    },
                    title: {
                      text: null
                    },
                    subtitle: {
                      text: null
                    },
                    plotOptions: {
                      series: {
                        dataLabels: {
                          enabled: true,
                          format: '{point.name}: {point.y:.1f}'
                        }
                      }
                    },
                    credits: {
                      enabled: false
                    },
                    tooltip: {
                      headerFormat: '<span style="font-size:11px">{series.name}</span><br>',
                      pointFormat: '<span style="color:{point.color}">{point.name}</span>: <b>{point.y:.2f}</b> of total<br/>'
                    }, "series": [{
                      "name": "Assets",
                      "colorByPoint": true,
                      "data": balances.map((cur, key) => ({ 
                        name: cur.symbol,
                        y: cur.available,
                      }))
                    }], 
                  }}
                />
              </div>
              <div className={`${ROOT_CLASS}__ratingvars__right`}>
                <div className={`${ROOT_CLASS}__infoblock`}>
                  <div className={`${ROOT_CLASS}__infoblocktitle`}>Balances:</div>
                  <div className={`${ROOT_CLASS}__infoblockcontent balances`}>
                    {
                      balances.map((cur, key) => 
                        <div className={`${ROOT_CLASS}__balances`} key={key}>
                          <div className={`${ROOT_CLASS}__balance_item`}>
                            { cur.symbol } : { cur.available }
                          </div>
                        </div>
                      )
                    }
                  </div>
                </div>
              </div>
            </div>  
          :
            <div className={`${ROOT_CLASS}__infoblock`}>
              <div className={`${ROOT_CLASS}__infoblocktitle`}>Balances:</div>
              <div className={`${ROOT_CLASS}__infoblockcontent`}>No data yet</div>
            </div>  
        }
        {/*<div className={`${ROOT_CLASS}__chart`}>
          <StockAreaChart
            width={681}
            height={384}
            type="hybrid"
            zoom={false}
            data={props.history}
            stockProps={{
              stroke: "#494a4e",
              tickStroke: "rgba(255, 255, 255, 0.2)"
            }}
            axes
          />
        </div>*/}
        <div className={`${ROOT_CLASS}__footer-block`}>
          {/*<button
            className={buildButtonClass(props)}
            onClick={props.onClickConnect}
          >
            { objectLangs[lng]['PopUpProductPage#4'] }
          </button>
          <button
            className={buildDisconnectClass(props)}
            onClick={props.onClickDisconnect}
          >
            { objectLangs[lng]['PopUpProductPage#5'] }
          </button>*/}
        </div>
      </div>
    )
  }
}

const buildDisconnectClass = (props) => {
  const buttonClass = `${ROOT_CLASS}__disconnect-button`;
  return `${buttonClass} ${isNeedHideDisc(props) ? buttonClass + '_hide' : ''}`;
}

const isNeedHideDisc = (props) => {
  return !props.personalInfo ||
    props.personalInfo.role === 'trader' || props.isNeedHideDisc;
}

const buildButtonClass = (props) => {
  const buttonClass = `${ROOT_CLASS}__footer-button`;
  return `${buttonClass} ${isNeedHide(props) ? buttonClass + '_hide' : ''}`;
}

const isNeedHide = (props) => {
  return !props.personalInfo ||
    props.personalInfo.role === 'trader' || props.isNeedHide || !props.hasKey;
}



